/*Blocks*/
.block{
	padding: 15px;
	margin-bottom: 15px;

	h3{
		margin-top: 0;
		padding-top: 0;
	}
}

.block-primary{
	@extend .block;
	@include add-background($primary-color);
}

.block-secondary{
	@extend .block;
	@include add-background($secondary-color);
}

.block-light{
	@extend .block;
	@include add-background($light-color);
}

.block-dark{
	@extend .block;
	@include add-background($dark-color);
}

.block-center{
	text-align: center !important;
}